import React, { FC } from "react";
import TextInput from "../../../UI/Input/TextInput";
import { Link } from "react-router-dom";
import { categories } from "../../../../helpers/data/categories";
import Button from "../../../UI/Button/Button";
import logo from "../../../../assets/logo-white.svg";
import whatsapp from "../../../../assets/whatsapp.png";
import telegram from "../../../../assets/telegram.png";
import visa from "../../../../assets/visa.png";
import mastercard from "../../../../assets/mastercard.png";
import s from "./DesktopFooter.module.scss";

interface Props {

}

const DesktopFooter: FC<Props> = ({ }) => {
  return (
    <div className={s.container}>
      <div className={s.mainInfo}>
        <div className={s.imgWrapper}>
          <img src={logo} />
        </div>
        <p className={s.description}>       " "     </p>
        <p className={s.subscribe}>    </p>
        <TextInput placeholder="  email" />
      </div>
      <div className={s.navigation}>
        <p className={s.title}> :</p>
        <Link to="/about" className="link"> </Link>
        <Link to="/payment-and-delivery" className="link">  </Link>
        <Link to="/refund" className="link"></Link>
        <Link to="/contacts" className="link"></Link>
      </div>
      <div className={s.categories}>
        <p className={s.title}>:</p>
        {
          categories.map(category => <Link key={category.id} className="link" to={category.to}>{category.title}</Link>)
        }
      </div>
      <div className={s.priceList}>
        <p className={s.title}>-:</p>
        <Link to="/admin" className="link">
          <Button></Button>
        </Link>
        <p className={s.subtitle}>  </p>
        <div className={s.messengersContainer}>
          <img src={whatsapp} />
          <img src={telegram} />
        </div>
      </div>
      <div className={s.contacts}>
        <p className={s.title}>:</p>
        <p className={s.number}>+7 (777) 490-00-91</p>
        <p className={s.workHours}> : 9:00-20:00</p>
        <p className={s.orderCall}> </p>
        <p className={s.mail}>opt.sultan@mail.ru</p>
        <p className={s.text}>    </p>
        <div className={s.cards}>
          <img src={visa} />
          <img src={mastercard} />
        </div>
      </div>
    </div>
  )
};

export default DesktopFooter;
